    state.page = 0; // Ð¡Ñ‚Ð°Ð²Ð¸Ð¼ Ð·Ð½Ð°Ñ‡ÐµÐ½Ð¸Ðµ Ñ‚ÐµÐºÑƒÑ‰ÐµÐ¹ ÑÑ‚Ñ€Ð°Ð½Ð¸Ñ†Ñ‹ Ð² 0

    PaginationView.render(state); // Ð ÐµÐ½Ð´ÐµÑ€Ð¸Ð¼ Ð¿Ð°Ð³Ð¸Ð½Ð°Ñ†Ð¸ÑŽ
