import { API_KEY, TIMEOUTSEC } from "./config.js";

const timeout = function (s) {
  // Ð•ÑÐ»Ð¸ Ð² Ñ‚ÐµÑ‡ÐµÐ½Ð¸Ð¸ n ÑÐµÐºÑƒÐ½Ð´ Ð½Ð¸Ñ‡ÐµÐ³Ð¾ Ð½Ðµ Ð¿Ñ€Ð¾Ð¸Ð·Ð¾Ð¹Ð´ÐµÑ‚, ÑÑ‚Ð¾Ñ‚ Ð¿Ñ€Ð¾Ð¼Ð¸Ñ Ð²Ñ‹Ð´Ð°ÑÑ‚ Reject Ñ Ð¾ÑˆÐ¸Ð±ÐºÐ¾Ð¹
  return new Promise(function (_, reject) {
    setTimeout(function () {
      reject(new Error(`Request took too long! Timeout after ${s} second`));
    }, s * 1000);
  });
};

const getJSON = async (url) => {
  const res = await Promise.race([fetch(url), timeout(TIMEOUTSEC)]); // Ð•ÑÐ»Ð¸ Ð² Ñ‚ÐµÑ‡ÐµÐ½Ð¸Ð¸ n ÑÐµÐºÑƒÐ½Ð´ Ð´Ð°Ð½Ð½Ñ‹Ðµ Ð½Ðµ ÑƒÑÐ¿ÐµÑŽÑ‚ Ð·Ð°Ð³Ñ€ÑƒÐ·Ð¸Ñ‚ÑÑ, Ñ‚Ð¾ Ð¿Ñ€Ð¾Ð¼Ð¸Ñ Ð²Ñ‹Ð´Ð°ÑÑ‚ reject
  if (!res.ok) throw new Error(res.statusText);

  return await res.json();
};

const sendJSON = async (url, data) => {
  const options = {
    method: "POST",
    headers: {
      "Content-Type": "application/json",
    },
    body: JSON.stringify(data),
  };

  const res = await Promise.race([
    fetch(`${url}?key=${API_KEY}`, options),
    timeout(TIMEOUTSEC),
  ]); // Ð•ÑÐ»Ð¸ Ð² Ñ‚ÐµÑ‡ÐµÐ½Ð¸Ð¸ n ÑÐµÐºÑƒÐ½Ð´ Ð´Ð°Ð½Ð½Ñ‹Ðµ Ð½Ðµ ÑƒÑÐ¿ÐµÑŽÑ‚ Ð·Ð°Ð³Ñ€ÑƒÐ·Ð¸Ñ‚ÑÑ, Ñ‚Ð¾ Ð¿Ñ€Ð¾Ð¼Ð¸Ñ Ð²Ñ‹Ð´Ð°ÑÑ‚ reject
  if (!res.ok) throw new Error(res.statusText);

  return await res.json();
};

export { timeout, getJSON, sendJSON };
